import React from 'react'
import { Link } from 'gatsby'

import Layout from '../components/layout'

const SecondPage = () => (
  <Layout>
    <h2>Dear Citizens of the world,</h2>
    <h3>Earth needs you..</h3>
    <p>Given the ability to communicate to anyone in the world through technology.. we all have the power and responsibility to convince all heads of states to act upon what could be the most catastrophic event of our lifetime.</p>
    <p>We need all of us to become more productive in this fight to save the world.</p>
    
    <p>
    	If you fail to recoginize the importance of this argument..
	</p>
    <ul>
    	<li><a href="https://www.youtube.com/watch?v=W5c59qUUnAY">Watch: The pale blue dot by Carl Sagan</a></li>
    	<li><a href="https://www.youtube.com/watch?v=8MqTOEospfo">Watch: Science in America by Neil deGrasse Tyson</a></li>
    	<li><a href="https://www.youtube.com/watch?v=SBjtO-0tbKU">Watch: The reality of climate change by </a></li>
    	<li><a href="http://www.ipcc.ch/report/sr15/">Read: IPCC report on climate change by UN</a></li>
    </ul>
    
	<p>
		We are on track to destroying the world's ecological balance and creating unnecessary wars over scarcity of water and food this century.. Let us influence President Donald Trump (and other world leaders) to do the right thing.. 
	</p>
  


    <Link to="/">#dearpresidentdonaldtrump</Link>
  </Layout>
)

export default SecondPage
